
#multi-selection-wrapper {
  position: absolute;
  left: 50%;
  top: 0;
}
#multiple-selection {
  position: absolute;
  outline: 1px solid red;
  background-color: rgba(216, 216, 216, 0.1);
  z-index: 99;
  cursor: pointer;
}



